import React from 'react';
import { Skeleton } from '@material-ui/lab';
import { Grid, Divider } from '@material-ui/core';

export default function HomePlaceholder() {
  return (
    <div id="Home">
      <Grid direction="row" container spacing={4}>
        <Grid item xs={12} lg={6} md={6}>
          <div className="about">
            <Skeleton variant="rect" width="100%" height={176} />
          </div>
        </Grid>
        <Grid item xs={12} lg={6} md={6}>
          <div className="contact">
            <div style={{ padding: "16px 16px" }}><Skeleton variant="circle" width={40} height={40} /></div>
            <div style={{ padding: "16px 16px" }}><Skeleton variant="circle" width={40} height={40} /></div>
            <div style={{ padding: "16px 16px" }}><Skeleton variant="circle" width={40} height={40} /></div>
            <div style={{ padding: "16px 16px" }}><Skeleton variant="circle" width={40} height={40} /></div>
          </div>
        </Grid>
      </Grid>
      <Divider />
      <div className="extra">
        <Skeleton className="extra-item" variant="rect" width="100%" height={176} />
      </div>
    </div>
  );
}
