import React, { useState, useEffect } from "react";
import {
  Paper,
  Typography,
  List,
  ListItem,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  TableContainer,
} from "@material-ui/core";

import "./Background.scss";
import BackgroundPlaceholder from "./BackgroundPlaceholder";

const Edu = (e) => (
  <div key={e.name} className="info">
    <h2 className="info-flex info-title">
      {/* <i className="fas fa-hand-point-right fa-xs" /> */}
      <div>{e.name}</div>
    </h2>
    <div className="info-content">
      <div className="info-flex info-date">
        <i className="far fa-clock" />
        <div>{e.date}</div>
      </div>
      <div className="url">
        <div className="img">
          <img src={e.logo} alt={e.name} />
        </div>
        <a href={e.url}>Link</a>
      </div>
      <div className="info-degree">{e.degree}</div>
      <div className="info-gpa">GPA: {e.gpa}</div>
      {e.relevantsHead && e.relevants && (
        <div className="info-relevants">
          <div className="title">Relevant Coursework:</div>
          <TableContainer component={Paper}>
            <Table className="table" aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="left">{e.relevantsHead[0]}</TableCell>
                  <TableCell align="center">{e.relevantsHead[1]}</TableCell>
                  <TableCell align="center">{e.relevantsHead[2]}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {e.relevants.map((row) => (
                  <TableRow key={row[0]}>
                    <TableCell align="left">{row[0]}</TableCell>
                    <TableCell align="center">{row[1]}</TableCell>
                    <TableCell align="center">{row[2]}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      )}
    </div>
  </div>
);

const Exp = (e) => (
  <div key={e.name} className="info">
    <h2 className="info-flex info-title">
      {/* <i className="fas fa-circle-notch fa-xs" /> */}
      <div>{e.role}</div>
    </h2>
    <div className="info-content">
      <div className="info-flex info-date">
        <i className="far fa-clock" />
        <div>{e.date}</div>
      </div>
      <div className="info-flex info-date">
        <i className="far fa-compass" />
        <div>{e.location}</div>
      </div>
      <div className="url">
        <div className="img">
          <img src={e.logo} alt={e.name} />
        </div>
        <a href={e.url}>{e.name}</a>
      </div>
      <div className="info-desc">
        {e.description && e.description.map((d) => <div key={d}>{d}</div>)}
      </div>
    </div>
  </div>
);

const Award = (e) => (
  <div key={e.name} className="info">
    <h2 className="info-flex info-title">
      {/* <i className="fas fa-circle-notch fa-xs" /> */}
      <div>{e.title}</div>
    </h2>
    <div className="info-content">
      <div className="info-flex info-date">
        <i className="far fa-clock" />
        <div>{e.date}</div>
      </div>
      <div className="info-flex info-date">
        <i className="far fa-compass" />
        <div>{e.location}</div>
      </div>
      <div className="url">
        <div className="img">
          <img src={e.logo} alt={e.name} />
        </div>
        <a href={e.url}>{e.name}</a>
      </div>
      <div className="info-desc">
        {e.description && e.description.map((d) => <div key={d}>{d}</div>)}
      </div>
      {
        e.cert &&
        (
          <div className="cert">
            <a href={e.cert} target="_blank" rel="noopener noreferrer">certificate</a>
          </div>
        )
      }
    </div>
  </div>
);

const paperLists = (data) => {
  return Object.keys(data || {}).map((paper) => {
    const { title, icon, items, code } = data[paper];
    return (
      <Paper key={title} className="data">
        <Typography color="primary" variant="h5" component="h3">
          <i className={icon} />
          &nbsp; {title}
        </Typography>
        {
          <List className="data-item">
            {(items || {}).map((item) => {
              return (
                <ListItem key={item.name}>
                  <div className="work">
                    {code === "edu" && Edu(item)}
                    {code === "exp" && Exp(item)}
                    {code === "award" && Award(item)}
                  </div>
                </ListItem>
              );
            })}
          </List>
        }
      </Paper>
    );
  });
};

export default function Background(props) {
  const { loader } = props;
  const [data, setData] = useState(null);
  const scrollY = window.scrollY;
  useEffect(() => {
    if (scrollY) {
      setTimeout(() => {
        window.scrollTo(0, 0);
      }, 100);
    }
  }, [scrollY]);
  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("./assets/data/background.json");
      const data = await res.json();
      setData(data);
    };
    loader(true);
    fetchData();
    loader(false);
  }, [loader]);
  if (!data) {
    return <BackgroundPlaceholder />;
  }
  return <div id="Background">{paperLists(data)}</div>;
}
