import React from 'react';
import { Skeleton } from '@material-ui/lab';
import { Grid } from '@material-ui/core';

export default function ProjectsPlaceholder() {
  return (
    <div id="Projects">
      <Grid container spacing={2} className="data">
        <Grid item xs={12} lg={6} md={6} className="data-item">
          <Skeleton className="data-item" variant="rect" width="100%" height={350} />
        </Grid>
        <Grid item xs={12} lg={6} md={6} className="data-item">
          <Skeleton className="data-item" variant="rect" width="100%" height={350} />
        </Grid>
        <Grid item xs={12} lg={6} md={6} className="data-item">
          <Skeleton className="data-item" variant="rect" width="100%" height={350} />
        </Grid>
        <Grid item xs={12} lg={6} md={6} className="data-item">
          <Skeleton className="data-item" variant="rect" width="100%" height={350} />
        </Grid>
      </Grid>
    </div>
  );
}
