import React, { useEffect, useState } from 'react'
import "./Resume.scss";
import { Grid, Chip } from '@material-ui/core';

export default function Resume({ imgPath }) {
  const [data, setData] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      const allData = {};
      let res = await fetch("./assets/data/main.json");
      let data = await res.json();
      allData.main = data;
      res = await fetch("./assets/data/home.json");
      data = await res.json();
      allData.home = data;
      res = await fetch("./assets/data/background.json");
      data = await res.json();
      allData.background = data;
      res = await fetch("./assets/data/skills.json");
      data = await res.json();
      allData.skills = data;
      res = await fetch("./assets/data/projects.json");
      data = await res.json();
      allData.projects = data;
      setData(allData);
    };
    fetchData();
  }, []);

  const { main, home, background, skills, projects } = data || {};

  if (!data) return (<div></div>);
  return (
    <div id="Resume">
      <div className="top">
        <div className="avatar">
          <img src={`${imgPath}${main.avatarResume}`} />
        </div>
        <div className="info">
          <div className="name">{main.name}</div>
          <div className="job">{main.job}</div>
          <div className="contacts">
            <div className="contact location">
              <i className="fas fa-map-marker-alt" /> &nbsp;
              <span>Iran, Tehran</span>
            </div>
            <div className="contact email">
              <i className={home.about.contact.Email.icon} /> &nbsp;
              <a href={home.about.contact.Email.link}>{home.about.contact.Email.text.replace(" [at-sign] ", "@")}</a>
            </div>
            <div className="contact telegram">
              <i className={home.about.contact.Telegram.icon} /> &nbsp;
              <a href={home.about.contact.Telegram.link}>{home.about.contact.Telegram.text}</a>
            </div>
            <div className="contact website">
              <i className="fas fa-globe-asia" /> &nbsp;
              <a href="https://alireza-kavian.github.io">alireza-kavian.github.io</a>
            </div>
          </div>
        </div>
      </div>
      <div className="left">
        <div className="content">
          <div className="about">
            <h3 className="color-primary">ABOUT</h3>
            <p dangerouslySetInnerHTML={{ __html: home.about.description.slice(1).join(" ") }} />
          </div>
          <div className="education">
            <h3 className="color-primary">EDUCATION</h3>
            {
              (() => {
                const e = background[0].items[1]
                return (
                  <div className="item">
                    <h4 className="title">
                      <a href={e.url}>{e.name}</a>
                    </h4>
                    <div className="body">
                      <div className="date">
                        <div>{e.date}</div>
                      </div>
                      <div className="degree">{e.degree}</div>
                      <div className="gpa">GPA: {e.gpa}</div>
                    </div>
                  </div>
                )
              })()
            }
          </div>
          <div className="experience">
            <h3 className="color-primary">EXPERIENCES</h3>
            {
              background[1].items.map((e, idx) => {
                return (
                  <div key={idx} className="item">
                    <h4 className="title">
                      <a href={e.url}>{e.name}</a>
                    </h4>
                    <h5 className="role">{e.role}</h5>
                    <div className="body">
                      <div className="date">
                        <div>{e.date}</div>
                      </div>
                    </div>
                  </div>
                )
              })
            }
          </div>
        </div>
      </div>
      <div className="right">
        <div className="content">
          <div className="projects">
            <h3 className="color-link">MAJOR PROJECTS</h3>
            {
              Object.keys(projects).map((k) => {
                const y = projects[k];
                const works = y.work;
                return works.map((w) => {
                  if (!w.star) return null;
                  return (
                    <div className="work">
                      <div className="name"><a href={w.url}>{w.name}</a></div>
                      <div className="for">{w.for}</div>
                      <div className="desc">{w.desc}</div>
                    </div>
                  )
                })
              })
            }
          </div>
          <div className="skills">
            <h3 className="color-link">MAJOR SKILLS</h3>
            <Grid justify="center" container spacing={1}>
              {
                Object.keys(skills).map((k) => {
                  const sk = skills[k];
                  return (sk || []).map((skill, idx) => {
                    if (!skill.show) return null;
                    return (
                      <Grid key={idx} item xs={4}>
                        <Chip className="skill" label={skill.name} />
                      </Grid>
                    )
                  })
                })
              }
            </Grid>
            <h3 className="color-link">LANGUAGES</h3>
            <Grid justify="center" container spacing={1}>
              {
                (skills.language).map((lang, idx) => {
                  return (
                    <Grid key={idx} item xs={3}>
                      <Chip className="skill" label={lang.name} />
                    </Grid>
                  )
                })
              }
            </Grid>
          </div>
        </div>
      </div>
    </div>
  )
}
