import React, { useState, useEffect } from 'react';
import {
  Paper,
  Typography,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Divider,
  Link,
  Grid,
  Button,
  Chip,
} from '@material-ui/core';

import "./Home.scss";
import HomePlaceholder from './HomePlaceholder';

const descriptionPaper = (about) => {
  const { descriptionTitle, description } = about;
  return (
    <div className="about">
      {
        descriptionTitle &&
        (
          <Typography variant="h5" component="h3">
            This is a sheet of paper.
          </Typography>
        )
      }
      {
        (description || []).map((text, idx) => (
          <Typography style={{ margin: "8px 0" }} key={`description-text-${idx + 1}`} component="div">
            <div dangerouslySetInnerHTML={{ __html: text }} />
          </Typography>
        ))
      }
    </div>
  );
}

const contactList = (about) => {
  const { contact } = about;
  return (
    <List className="contact">
      {
        Object.keys(contact || {}).map((item) => {
          const text = contact[item].text;
          const icon = contact[item].icon;
          const link = contact[item].link;
          return (
            <ListItem key={item}>
              <ListItemAvatar>
                <Avatar>
                  <i className={icon} />
                </Avatar>
              </ListItemAvatar>
              <ListItemText primary={<a target="_blank" rel="noopener noreferrer" href={link}>{text}</a>} secondary={item} />
            </ListItem>
          );
        })
      }
    </List>
  )
}

const extrasPaperLists = (extras) => {
  return Object.keys(extras || {}).map((extra) => {
    const { title, icon, items } = extras[extra];
    return (
      <Paper key={title} className="extra">
        <Typography color="primary" variant="h5" component="h3">
          <i className={icon} />&nbsp; {title}
        </Typography>
        {
          <List className="extra-item">
            {
              (items || {}).map((item) => {
                return (
                  <ListItem key={item}>
                    {
                      item[1] &&
                      <Link href={item[1]}>
                        <ListItemText primary={item[0]} />
                      </Link>
                    }
                    {
                      !item[1] &&
                      <ListItemText primary={item[0]} />
                    }
                  </ListItem>
                );
              })
            }
          </List>
        }
      </Paper>
    );
  });
}

export default function Home(props) {
  const { loader } = props;
  const [data, setData] = useState(null);
  const scrollY = window.scrollY;
  useEffect(() => {    
    if(scrollY){
      setTimeout(() => {
        window.scrollTo(0, 0);
      }, 100);
    }
  }, [scrollY]);
  useEffect(() => {
    const fetchData = async () => {
      const res = await fetch("./assets/data/home.json");
      const data = await res.json();
      setData(data);
    };
    loader(true);
    fetchData();
    loader(false);
  }, [loader]);
  if (!data) {
    return (
      <HomePlaceholder />
    );
  }
  return (
    <div id="Home">
      <Grid direction="row" container spacing={0}>
        <Grid item xs={12} lg={5} md={6}>
          {descriptionPaper(data.about || {})}
        </Grid>
        <Grid item xs={12} lg={4} md={6}>
          {contactList(data.about || {})}
        </Grid>
        <Grid item xs={12} lg={3} md={4}>
          <div className="more">
            <Chip
              className="item chip"
              icon={<i className="fas fa-user-clock" />}
              label={<span>{(new Date().getFullYear()) - 1997}</span>}
            />
            <Chip
              className="item chip"
              icon={<i className="fas fa-map-marker-alt" />}
              label={<span>Tehran, Iran</span>}
            />
            <Button className="item" color="primary" variant="contained"><a href="/assets/resume/cv.pdf">Academic CV</a></Button>
            <Button className="item" color="primary" variant="outlined"><a href="/assets/resume/resume.pdf">Resume</a></Button>
          </div>
        </Grid>
      </Grid>
      <Divider />
      {extrasPaperLists(data.extras || {})}
    </div>
  );
}
