import { createMuiTheme } from '@material-ui/core/styles';
import { red } from '@material-ui/core/colors';

const theme = createMuiTheme({
  typography: {
    useNextVariants: true,
    fontFamily: 'Roboto',
    fontSize: 14
  },
  palette: {
    primary: {
      main: '#222525',
      contrastText: '#fff',
    },
    secondary:{
      main: "#ededed",
      contrastText: "#111"
    },
    danger: {
      main: red[500],
      light: red[100]
    },
  },
});

export default theme;